<template>
<v-app id="inspire" >
  <div class="container">
    <div>
      <b-table
        striped
        hover
        :items="items"
        :per-page="perPage"
        :current-page="currentPage"
        :fields="fields"
        @row-clicked="rowClick"
      ></b-table>
      <b-pagination v-model="currentPage" :total-rows="rows" :per-page="perPage" align="center"></b-pagination>
      <b-button @click="writeContent"></b-button>
    </div>
  </div>
   <v-footer app
    color="#B677FF"
    height="60px">
      <span class="footer_s">&copy; {{ new Date().getFullYear() }}    . </span>
    </v-footer>
</v-app>
</template>

<script>
import data from "@/data";

export default {
  name: "Test",
  data() {
    //  : https://blog.naver.com/haskim0716n/221681695401
    let contentItems = data.Content.sort((a, b) => {
      return b.content_id - a.content_id;
    }); // 

    // User  Content  user_id     Content   + 'user_name'  .
    let items = contentItems.map((contentItem) => {
      return {
        ...contentItem,
        user_name: data.User.filter((userItem) => {
          return contentItem.user_id === userItem.user_id;
        })[0].name,
      };
    });

    return {
      currentPage: 1, //  
      perPage: 10, //   
      // bootstrap 'b-table'  
      fields: [
        {
          key: "content_id",
          label: "num",
        },
        {
          key: "title",
          label: "",
        },
        {
          key: "user_name",
          label: "",
        },
        {
          key: "created_at",
          label: "",
        },
      ],
      items: items,
    };
  },
  methods: {
    rowClick(item, index, e) {
      this.$router.push({
        path: `/board/detail/${item.content_id}`,
      });
    },
    writeContent() {
      this.$router.push({
        path: `/board/create`,
      });
    },
  },
  computed: {
    rows() {
      return this.items.length;
    },
  },
};
</script>

<style>
.container {
  width: 70%;
  height: 70%;
  margin: 30% auto;
}
</style>